import React, { useRef, useEffect, useState } from "react";
import HTMLFlipBook from "react-pageflip";
import "./Flipbook.css";

const SilentBattleBooklet = () => {
  const totalPages = 22;
  const pages = [];
  const flipBookRef = useRef(null);
  const containerRef = useRef(null);
  const [hasBeenOpened, setHasBeenOpened] = useState(false);

  useEffect(() => {
    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting && !hasBeenOpened) {
            // When book enters viewport and hasn't been opened yet, open to first spread
            setTimeout(() => {
              if (flipBookRef.current) {
                flipBookRef.current.pageFlip().flipNext(); // Open first page to show first spread
                setHasBeenOpened(true); // Mark as opened so it won't trigger again
                
                // Disconnect observer after first trigger to prevent future automatic page turns
                observer.disconnect();
              }
            }, 300);
          }
        });
      },
      {
        threshold: 0.3, // Trigger when 30% of the book is visible
        rootMargin: '0px'
      }
    );

    if (containerRef.current && !hasBeenOpened) {
      observer.observe(containerRef.current);
    }

    return () => {
      observer.disconnect();
    };
  }, [hasBeenOpened]);

  for (let i = 1; i <= totalPages; i++) {
    pages.push(
      <div className="page" key={i}>
        <img
          src={`/booklet-silent-battle/silent-page-${i}.png`}
          alt={`Silent Battle Page ${i}`}
          style={{ width: "100%", height: "100%", objectFit: "cover" }}
        />
      </div>
    );
  }

  return (
    <div className="flipbook-container" ref={containerRef}>
      <HTMLFlipBook
        ref={flipBookRef}
        width={469}
        height={625}
        size="fixed"
        minWidth={313}
        maxWidth={625}
        minHeight={406}
        maxHeight={813}
        maxShadowOpacity={0.5}
        showCover={true}
        mobileScrollSupport={true}
      >
        {pages}
      </HTMLFlipBook>
    </div>
  );
};

export default SilentBattleBooklet;
